.src-components-product-product__star {
  height: 0px;
  width: 0px;
  margin-left: 10px;
  border-left: solid var(--size, 10px) transparent;
  border-right: solid var(--size, 10px) transparent;
  border-bottom: solid calc(var(--size, 10px) * 2) var(--color, black);
  position: relative; }

.src-components-product-product__star:after {
  height: 0px;
  width: 0px;
  position: absolute;
  border-left: solid var(--size, 10px) transparent;
  border-right: solid var(--size, 10px) transparent;
  border-top: solid calc(var(--size, 10px) * 2) var(--color, black);
  content: "";
  top: calc(var(--size, 10px) / 2);
  left: calc(var(--size, 10px) * -1); }
